.admin-login-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    height: 40vh;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    min-width: 360px;
}

.admin-login-inner-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 240px;
    background-color: #DCDCDC;
    padding: 20px;
    border:2px solid black;
    border-radius: 1%;
    min-width: 240px;
}

.login-button-container {
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        padding: 5px;
}

.login-button {
    margin: 0 5px;
    width: 160px;
}

@media screen and (min-width:600px) {
